<template>
  <div class="auth">
    <div class="auth__wrap" v-show="stateauth == 1">
      <div class="auth__text  mt-8 mb-8">Введите номер телефона</div>
      <input
        type="text"
        class="input-field align-center big-spacing"
        placeholder="+7(•••)•••••••"
        v-mask="'+7 (###) ###-##-##'"
        :class="{ 'input-field--error': $v.form.phone.$error }"
        v-model="$v.form.phone.$model"
      />
      <div class="text-error" v-show="$v.form.phone.$error">
        Вы ввели неверный номер
      </div>

      <button
        class="auth__btn button-primary"
        :disabled="!$v.form.phone.$dirty || $v.form.phone.$error"
        @click="sendPhoneCode"
      >
        Выслать код
      </button>
    </div>
    <div class="auth__wrap" v-show="stateauth == 2">
      <div class="auth__phone-text   mt-10 mb-10">
        {{ form.phone }}
      </div>
      <div class="auth__text  mt-8 mb-8">
        Введите полученный код из СМС
      </div>
      <input
        type="text"
        class="input-field align-center big-spacing"
        :class="{ 'input-field--error': $v.form.sms.$error }"
        placeholder="••••"
        v-mask="'####'"
        v-model="$v.form.sms.$model"
      />
      <div class="text-error" v-show="$v.form.sms.$error">
        Вы ввели неверный код
      </div>
      <button
        class="auth__btn button-primary"
        :disabled="!$v.form.sms.$dirty || $v.form.sms.$error"
        @click="sendSmsCode"
      >
        Отправить
      </button>

      <vac :left-time="60000" ref="countDown" :autoStart="false">
        <span slot="process" slot-scope="{ timeObj }">{{
          `Выслать код повторно через ${timeObj.s} с`
        }}</span>
        <a class="auth__link mt-10" slot="finish" @click="sendPhoneCode">
          Выслать код повторно</a
        >
      </vac>
    </div>
  </div>
</template>

<script>
import { required, minLength, numeric } from "vuelidate/lib/validators";
import { mapActions } from "vuex";

export default {
  name: "login",
  data() {
    return {
      stateauth: 1,
      form: {
        phone: "",
        sms: ""
      }
    };
  },
  validations: {
    form: {
      phone: {
        required,
        minLength: minLength(18)
      },
      sms: {
        required,
        numeric,
        minLength: minLength(4)
      }
    }
  },
  methods: {
    ...mapActions("auth", ["GET_ENTER_CODE", "VERIFY_GET_ENTER_CODE"]),
    sendPhoneCode() {
      this.GET_ENTER_CODE(this.form)
        .then(res => {
          this.stateauth = 2;
          this.$toasted.global.success(res.data.message);
          this.$refs.countDown.startCountdown(true);
        })
        .catch(error => {
          this.stateauth = 1;
          this.$toasted.global.error(error.response.data.message);
        });
    },
    sendSmsCode() {
      this.VERIFY_GET_ENTER_CODE(this.form)
        .then(response => {
          this.stateauth = 1;
          if (response.status === 201) {
            this.$router.push("/user-profile");
          } else {
            this.$router.push("/playbill");
          }
        })
        .catch(error => {
          this.$toasted.global.error(error);
        });
    }
  }
};
</script>

<style scoped></style>
