<template>
  <div class="main-page">
    <div class="main-page__nav">
      <div class="main-page__nav-head">
        <button
          class="button-primary"
          @click="openAuthModal"
          v-if="!isAuthenticated"
        >
          Войти
        </button>
      </div>

      <div class="main-page__user" v-if="isAuthenticated">
        <router-link to="/user-profile" class="user-info">
          <div class="user-info__photo">
            <img
              v-if="getUser.avatar.url"
              :src="getUser.avatar.url"
              alt="user"
              width="40"
              height="40"
            />
            <svg class="navbar__logo" v-if="!getUser.avatar.url">
              <use href="@/assets/img/sprite.svg#icon-anonymous-avatar"></use>
            </svg>
          </div>
          <div class="user-info__name">
            {{ getUser.full_name || "анонимный" }}
          </div>
          <div class="user-info__status">{{ getRole }}</div>
        </router-link>

        <div class="user-info__group-icon">
          <router-link to="/notification" class="badge-btn">
            <svg width="24" height="24">
              <use href="@/assets/img/sprite.svg#icon-notification"></use>
            </svg>
            <span
              class="badge"
              v-show="getUser.unread_notifications_count > 0"
              >{{ getUser.unread_notifications_count }}</span
            >
          </router-link>
          <router-link to="/favorites" class="badge-btn">
            <svg width="24" height="24">
              <use href="@/assets/img/sprite.svg#icon-bookmark-2"></use>
            </svg>
            <span
              class="badge badge--warning"
              v-show="getUser.favorite_count > 0"
            >
              {{ getUser.favorite_count }}
            </span>
          </router-link>
          <router-link to="/tickets" class="badge-btn">
            <svg width="24" height="24">
              <use href="@/assets/img/sprite.svg#icon-ticket"></use>
            </svg>
            <span class="badge badge--info">0</span>
          </router-link>
        </div>
      </div>

      <div class="main-page__nav-content">
        <nav-side-bar />
      </div>

      <div class="main-page__nav-search"></div>
    </div>
    <div class="main-page__bg-right">
      <router-link :to="'/playbill'" class="main-page__photo">
        <video autoplay muted loop class="main-page__video">
          <source src="/video/videoplayback.mp4" type="video/mp4" />
        </video>
        <svg width="70%" height="70%">
          <use href="@/assets/img/sprite.svg#logo"></use>
        </svg>
      </router-link>
    </div>
    <modal
      :show.sync="showModalRegistration"
      :centered="false"
      :show-close="true"
    >
      <login />
    </modal>
  </div>
</template>

<script>
import NavSideBar from "../components/Sidebar/NavSideBar";
import Modal from "../components/Modals/Modal";
import Login from "../components/auth/Login";
import { mapGetters } from "vuex";

const TABS = [
  {
    title: "Войти",
    value: "tab1"
  },
  {
    title: "Зарегистрироваться",
    value: "tab2"
  }
];

export default {
  name: "MainPage",
  components: {
    Login,
    Modal,
    NavSideBar
  },
  props: {
    login: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      showModalRegistration: false,
      tabs: TABS,
      currentTab: "tab1"
    };
  },
  methods: {
    handleClick(newTab) {
      this.currentTab = newTab;
    },
    openAuthModal() {
      this.showModalRegistration = true;
    }
  },
  mounted() {
    if (this.isAuthenticated) {
      this.showModalRegistration = false;
      this.$router.push("/");
    } else {
      this.showModalRegistration = this.login;
    }
  },
  computed: {
    ...mapGetters("user", ["getUser", "getRole"]),
    isAuthenticated() {
      return this.$store.getters["auth/isAuthenticated"];
    }
  }
};
</script>
